"use client";

import { useState } from "react";
import {
  Card,
  CardHeader,
  CardContent,
  CardFooter,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";

interface CalculationHistory {
  bnPages: number;
  colorPages: number;
  copies: number;
  duplex: boolean;
  binding: boolean;
  total: number;
  timestamp: Date;
}

export default function PrintingCostCalculator() {
  const [bnPages, setBnPages] = useState("");
  const [colorPages, setColorPages] = useState("");
  const [copies, setCopies] = useState("");
  const [isDuplex, setIsDuplex] = useState(false);
  const [hasBinding, setHasBinding] = useState(false);
  const [result, setResult] = useState<number | null>(null);
  const [errors, setErrors] = useState<{ bnPages?: string; colorPages?: string; copies?: string }>({});
  const [history, setHistory] = useState<CalculationHistory[]>([]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setErrors({});

    const numBnPages = parseFloat(bnPages) || 0;
    const numColorPages = parseFloat(colorPages) || 0;
    const numCopies = parseFloat(copies);
    let valid = true;
    const newErrors: { bnPages?: string; colorPages?: string; copies?: string } = {};

    if (numBnPages < 0) {
      newErrors.bnPages = "El número de páginas no puede ser negativo.";
      valid = false;
    }
    if (numColorPages < 0) {
      newErrors.colorPages = "El número de páginas no puede ser negativo.";
      valid = false;
    }
    if (numColorPages > numBnPages) {
      newErrors.colorPages = "Las páginas a color no pueden ser más que las páginas totales.";
      valid = false;
    }
    if (numBnPages === 0 && numColorPages === 0) {
      newErrors.bnPages = "Debe ingresar al menos una página.";
      valid = false;
    }
    if (isNaN(numCopies) || numCopies <= 0) {
      newErrors.copies = "Ingrese un número válido de copias.";
      valid = false;
    }

    if (!valid) {
      setErrors(newErrors);
      return;
    }

    // Calculate pages for each type
    const actualBnPages = numBnPages - numColorPages; // Páginas que quedan en blanco y negro
    const actualColorPages = numColorPages; // Páginas a color
    
    // Pricing: Color = 0.50 Bs, Black & White = 0.20 Bs
    let bnCostPerPage = 0.20;
    let colorCostPerPage = 0.50;
    
    // Apply duplex discount (20% off)
    if (isDuplex) {
      bnCostPerPage = bnCostPerPage * 0.8;
      colorCostPerPage = colorCostPerPage * 0.8;
    }
    
    // Calculate total
    const bindingFee = hasBinding ? 3 : 0;
    const bnTotal = actualBnPages * numCopies * bnCostPerPage;
    const colorTotal = actualColorPages * numCopies * colorCostPerPage;
    const total = bnTotal + colorTotal + bindingFee;
    setResult(total);

    // Add to history
    setHistory((prev) => [
      ...prev,
      { 
        bnPages: numBnPages, 
        colorPages: numColorPages,
        copies: numCopies, 
        duplex: isDuplex, 
        binding: hasBinding, 
        total,
        timestamp: new Date()
      },
    ]);
  };

  const clearHistory = () => {
    setHistory([]);
  };

  const resetForm = () => {
    setBnPages("");
    setColorPages("");
    setCopies("");
    setIsDuplex(false);
    setHasBinding(false);
    setResult(null);
    setErrors({});
  };

  return (
    <div className="min-h-screen bg-background p-4">
      <div className="container mx-auto max-w-4xl">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-foreground mb-2">
            Calculadora de Costes de Impresión
          </h1>
          <p className="text-muted-foreground">
            Calcula el coste total de tus documentos de forma rápida y precisa
          </p>
        </div>

        <div className="grid md:grid-cols-2 gap-6">
          {/* Calculator Card */}
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl">Calculadora</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Total Pages (B&W) */}
                <div className="space-y-2">
                  <Label htmlFor="bnPages" className="text-sm font-medium">
                    Total de Páginas (Bandeja Blanco y Negro)
                  </Label>
                  <Input
                    id="bnPages"
                    type="number"
                    value={bnPages}
                    onChange={(e) => setBnPages(e.target.value)}
                    placeholder="Ej: 100"
                    min="0"
                  />
                  {errors.bnPages && (
                    <span className="text-destructive text-sm">{errors.bnPages}</span>
                  )}
                </div>

                {/* Color Pages */}
                <div className="space-y-2">
                  <Label htmlFor="colorPages" className="text-sm font-medium">
                    Páginas a Color (Bandeja Color)
                  </Label>
                  <Input
                    id="colorPages"
                    type="number"
                    value={colorPages}
                    onChange={(e) => setColorPages(e.target.value)}
                    placeholder="Ej: 10"
                    min="0"
                  />
                  <p className="text-xs text-muted-foreground">
                    Estas páginas se restarán de las páginas en blanco y negro
                  </p>
                  {errors.colorPages && (
                    <span className="text-destructive text-sm">{errors.colorPages}</span>
                  )}
                </div>

                {/* Number of Copies */}
                <div className="space-y-2">
                  <Label htmlFor="copies" className="text-sm font-medium">
                    Número de Copias
                  </Label>
                  <Input
                    id="copies"
                    type="number"
                    value={copies}
                    onChange={(e) => setCopies(e.target.value)}
                    placeholder="Ej: 2"
                    min="1"
                  />
                  {errors.copies && (
                    <span className="text-destructive text-sm">{errors.copies}</span>
                  )}
                </div>

                {/* Duplex Printing */}
                <div className="flex items-center justify-between p-3 border rounded-md">
                  <div className="space-y-1">
                    <Label htmlFor="duplex" className="text-sm font-medium">
                      Impresión Dúplex
                    </Label>
                    <p className="text-xs text-muted-foreground">
                      Ahorra 20% en el coste por página
                    </p>
                  </div>
                  <Switch id="duplex" checked={isDuplex} onCheckedChange={setIsDuplex} />
                </div>

                {/* Binding */}
                <div className="flex items-center justify-between p-3 border rounded-md">
                  <div className="space-y-1">
                    <Label htmlFor="binding" className="text-sm font-medium">
                      Encuadernado
                    </Label>
                    <p className="text-xs text-muted-foreground">
                      Coste adicional de 3.00 Bs
                    </p>
                  </div>
                  <Switch id="binding" checked={hasBinding} onCheckedChange={setHasBinding} />
                </div>

                {/* Buttons */}
                <div className="flex gap-3">
                  <Button type="submit" className="flex-1">
                    Calcular Coste
                  </Button>
                  <Button type="button" variant="outline" onClick={resetForm}>
                    Limpiar
                  </Button>
                </div>
              </form>
            </CardContent>
            
            {result !== null && (
              <CardFooter className="bg-muted/50">
                <div className="w-full text-center">
                  <p className="text-sm text-muted-foreground mb-1">Coste Total</p>
                  <p className="text-3xl font-bold text-primary">
                    {result.toFixed(2)} Bs
                  </p>
                </div>
              </CardFooter>
            )}
          </Card>

          {/* History Card */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle className="text-2xl">Historial</CardTitle>
              {history.length > 0 && (
                <Button variant="outline" size="sm" onClick={clearHistory}>
                  Limpiar Historial
                </Button>
              )}
            </CardHeader>
            <CardContent>
              {history.length === 0 ? (
                <div className="text-center py-8">
                  <p className="text-muted-foreground">
                    No hay cálculos realizados aún
                  </p>
                </div>
              ) : (
                <div className="space-y-3 max-h-96 overflow-y-auto">
                  {history.map((item, idx) => (
                    <div key={idx} className="p-3 border rounded-md bg-card">
                      <div className="flex justify-between items-start mb-2">
                        <div className="text-sm">
                          <div><span className="font-medium">{item.bnPages}</span> páginas totales</div>
                          <div><span className="font-medium">{item.colorPages}</span> páginas a color</div>
                          <div><span className="font-medium">{item.copies}</span> copias</div>
                        </div>
                        <div className="text-lg font-bold text-primary">
                          {item.total.toFixed(2)} Bs
                        </div>
                      </div>
                      <div className="text-xs text-muted-foreground space-y-1">
                        <div>
                          B/N: {item.bnPages - item.colorPages} páginas | Color: {item.colorPages} páginas
                        </div>
                        <div className="flex gap-4">
                          <span>Dúplex: {item.duplex ? 'Sí' : 'No'}</span>
                          <span>Encuadernado: {item.binding ? 'Sí' : 'No'}</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Pricing Information */}
        <Card className="mt-6">
          <CardHeader>
            <CardTitle className="text-xl">Información de Precios</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid md:grid-cols-3 gap-4">
              <div className="space-y-2">
                <h3 className="font-semibold">Precios Base</h3>
                <ul className="text-sm space-y-1">
                  <li>• Blanco y Negro: 0.20 Bs por página</li>
                  <li>• Color: 0.50 Bs por página</li>
                  <li>• Encuadernado: 3.00 Bs por documento</li>
                </ul>
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold">Sistema de Bandejas</h3>
                <ul className="text-sm space-y-1">
                  <li>• Bandeja 1: Páginas en Blanco y Negro</li>
                  <li>• Bandeja 2: Páginas a Color</li>
                  <li>• Las páginas a color se restan de las B/N</li>
                </ul>
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold">Descuentos</h3>
                <ul className="text-sm space-y-1">
                  <li>• Impresión Dúplex: 20% de descuento</li>
                  <li>• El descuento se aplica al coste por página</li>
                </ul>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
